import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

/* Layout */
import Layout from '../layout/Layout'
import ComLayout from '../layout/ComLayout'
import DocsLayot from '../layout/DocsLayot'

const routes = [
  { path: '/404', component: () => import('@/views/404'), hidden: true },
  // {
  //   path: '/',
  //   component: Layout,
  //   redirect: '/components',
  //   hidden: true,
  //   name: 'home',
  // },
  {
    path: '/',
    component: ComLayout,
    hidden: true,
    children: [
      {
        path: '',
        name: 'Comindex',
        component: () => import('@/views/Com/index.vue'),
        meta: { title: '首页', icon: 'el-icon-s-marketing', requireAuth: false }
      }
    ]
  },
  {
    path: '/document',
    component: DocsLayot,
    hidden: true,
    children: [
      {
        path: '',
        name: 'ComApidocs',
        component: () => import('@/views/Com/ComApidocs.vue'),
        meta: { title: '开发者中心', icon: 'el-icon-s-marketing', requireAuth: false }
      }
    ]
  },
  {
    path: '/iot',
    component: Layout,
    children: [
      {
        path: '',
        name: 'index',
        component: () => import('@/views/Index'),
        meta: { title: '汇总', icon: 'el-icon-s-marketing', requireAuth: true }
      }
    ]
  },
  {
    path: '/iot/principal',
    component: Layout,
    meta: {
      title: '使用主体',
      icon: 'el-icon-s-grid'
    },
    children: [
      {
        path: '',
        name: 'principalMain',
        component: () => import('@/views/Principal/list.vue'),
        meta: { title: '全部', icon: 'el-icon-coordinate', requireAuth: true }
      },
      {
        path: 'new',
        name: 'principalNew',
        component: () => import('@/views/Principal/add.vue'),
        meta: { title: '新建', icon: 'el-icon-circle-plus', requireAuth: true }
      }
    ]
  },
  {
    path: '/iot/product',
    component: Layout,
    meta: { title: '农产品管理', icon: 'el-icon-s-help' },
    children: [
      {
        path: '',
        name: 'productAll',
        component: () => import('@/views/Product/allList.vue'),
        meta: { title: '全部农产品', icon: 'el-icon-pie-chart', requireAuth: true },
      },
      {
        path: 'new',
        name: 'productNew',
        component: () => import('@/views/Product/add.vue'),
        meta: { title: '添加农产品', icon: 'el-icon-circle-plus', requireAuth: true }
      },
      {
        path: 'detail',
        name: 'principalDetail',
        component: () => import('@/views/Product/detail.vue'),
        meta: { title: '添加农产品', icon: 'el-icon-circle-plus', requireAuth: true },
        hidden: true
      },
      {
        path: 'edit',
        name: 'principalEdit',
        component: () => import('@/views/Product/edit.vue'),
        meta: { title: '农产品参数', icon: 'el-icon-circle-plus', requireAuth: true },
        hidden: true
      },
    ]
  },
  {
    path: '/iot/gateway',
    component: Layout,
    meta: { title: '网关', icon: 'el-icon-place' },
    children: [
      {
        path: '',
        name: 'gatewayIndex',
        component: () => import('@/views/Gateway/list.vue'),
        meta: { title: '网关', icon: 'el-icon-pie-chart', requireAuth: true },
      },
      {
        path: 'add',
        name: 'gatewayAdd',
        component: () => import('@/views/Gateway/add.vue'),
        meta: { title: '添加网关', icon: 'el-icon-circle-plus', requireAuth: true }
      }
    ]
  },
  {
    path: '/iot/equipment',
    component: Layout,
    meta: { title: '设备', icon: 'el-icon-share' },
    children: [
      {
        path: '',
        name: 'equipmentMain',
        component: () => import('@/views/Equipment/list.vue'),
        meta: { title: '设备', icon: 'el-icon-share', requireAuth: true }
      },
      {
        path: 'add',
        name: 'equipmentAdd',
        component: () => import('@/views/Equipment/add.vue'),
        meta: { title: '添加设备', icon: 'el-icon-circle-plus', requireAuth: true },
        hidden: true
      },
      {
        path: 'params',
        name: 'equipmentParams',
        component: () => import('@/views/Equipment/params.vue'),
        meta: { title: '设备参数', icon: 'el-icon-share', requireAuth: true },
        hidden: true
      },
      {
        path: 'paramseditoradd',
        name: 'equipmentParamsEditOrAdd',
        component: () => import('@/views/Equipment/editanaddparams.vue'),
        meta: { title: '新增或编辑设备参数', icon: 'el-icon-share', requireAuth: true },
        hidden: true
      },
      {
        path: 'manager',
        name: 'equipmentManager',
        component: () => import('@/views/Equipment/manager.vue'),
        meta: { title: '设备类型管理', icon: 'el-icon-price-tag', requireAuth: true }
      },
    ]
  },
  {
    path: '/iot/paramtemplate',
    component: Layout,
    meta: { title: '传感器参数模板', icon: 'el-icon-s-operation' },
    children: [
      {
        path: '',
        name: 'paramtemplateCategory',
        component: () => import('@/views/ParamTemplate/list.vue'),
        meta: { title: '传感器模板管理', icon: 'el-icon-pie-chart', requireAuth: true },
      },
      {
        path: 'add',
        name: 'paramtemplateAdd',
        component: () => import('@/views/ParamTemplate/add.vue'),
        meta: { title: '添加传感器模板', icon: 'el-icon-circle-plus', requireAuth: true }
      },
      {
        path: 'itemlist',
        name: 'paramtemplateItemlist',
        component: () => import('@/views/ParamTemplate/itemlist.vue'),
        meta: { title: '参数', icon: 'el-icon-circle-plus', requireAuth: true },
        hidden: true
      }
    ]
  },
  {
    path: '/iot/opendata',
    component: Layout,
    meta: {
      title: 'OpenApi',
      icon: 'el-icon-connection'
    },
    children: [
      {
        path: '',
        name: 'opendataIndex',
        component: () => import('@/views/Index'),
        meta: { title: '使用情况', icon: 'el-icon-s-data', requireAuth: true }
      },
      {
        path: 'secretkey',
        name: 'opendataSecretKey',
        component: () => import('@/views/Opendata/Index'),
        meta: { title: '授权管理', icon: 'el-icon-user-solid', requireAuth: true }
      }
    ]
  },
  {
    path: '/iot/apis',
    component: Layout,
    adminpage: true,
    meta: {
      title: '对外文档管理',
      icon: 'el-icon-position'
    },
    children: [
      {
        path: '',
        name: 'apisIndex',
        component: () => import('@/views/Apidocs/all.vue'),
        meta: { title: '全部', icon: 'el-icon-s-data', requireAuth: true }
      },
      {
        path: 'add',
        name: 'apisAdd',
        component: () => import('@/views/Apidocs/addoredit.vue'),
        meta: { title: '新增', icon: 'el-icon-circle-plus', requireAuth: true }
      }
    ]
  },
  {
    path: '/iot/cropsclass',
    component: Layout,
    meta: {
      title: '种植类目',
      icon: 'el-icon-connection'
    },
    children: [
      {
        path: '',
        name: 'cropsdata',
        component: () => import('@/views/Index'),
        meta: { title: '种植数据', icon: 'el-icon-s-data', requireAuth: true },
        hidden: true
      },
      {
        path: 'Index',
        name: 'cropsclassIndex',
        component: () => import('@/views/cropsdata/Index'),
        meta: { title: '现有种类', icon: 'el-icon-coin', requireAuth: true },
      },
      {
        path: 'detail',
        name: 'cropsclassDetail',
        component: () => import('@/views/cropsdata/detail'),
        meta: { title: '农作物', icon: 'el-icon-coin', requireAuth: true },
        hidden: true
      },
      {
        path: 'add',
        name: 'cropsclassAdd',
        component: () => import('@/views/cropsdata/add'),
        meta: { title: '新增种类', icon: 'el-icon-circle-plus', requireAuth: true }
      }
    ]
  },
  {
    path: '/iot/account',
    component: Layout,
    meta: {
      title: '账户管理',
      icon: 'el-icon-s-custom'
    },
    adminpage: true,
    children: [
      {
        path: '',
        name: 'accountIndex',
        component: () => import('@/views/Account/list.vue'),
        meta: { title: '全部账户', icon: 'el-icon-s-data', requireAuth: true }
      },
      {
        path: 'add',
        name: 'accountAdd',
        component: () => import('@/views/Account/add.vue'),
        meta: { title: '新增账户', icon: 'el-icon-circle-plus', requireAuth: true }
      }
    ]
  },
  {
    path: '/iot/myset',
    component: Layout,
    children: [
      {
        path: '',
        name: 'mysetIndex',
        component: () => import('@/views/Setmyacc'),
        meta: { title: '个人设置', icon: 'el-icon-thumb', requireAuth: true }
      }
    ]
  },
  {
    path: '/login',
    name: 'login',
    hidden: true,
    component: () => import('@/views/login/'),
    meta: { title: '登录你的账户', icon: 'el-icon-thumb', requireAuth: false }
  }
]

const router = new VueRouter({
  mode: 'history',
  routes
})

export default router

const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}